import React from 'react';
import { Link } from 'react-router-dom';

interface Props {
  title: string;
  page: string;
  path: string;
  imgSrc: string;
}

const Card: React.FC<Props> = (props: Props) => (
  <div className="card">
    <div className="front center">
      <Link to={props.path}>
        <img src={props.imgSrc} alt={`${props.title}-image`} className={`${props.page}-image`} />
        <h3>{props.title}</h3>
      </Link>
    </div>
  </div>
);

export default Card;
